"use client";

import { useEffect, useState } from "react";
import { Accordion, AccordionSummary, AccordionDetails } from "@mui/material";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import AddIcon from "@mui/icons-material/Add";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import SaveIcon from "@mui/icons-material/Save";
import CloseIcon from "@mui/icons-material/Close";

export default function DropdownConfigPage() {
  const [data, setData] = useState([]);
  const [modules, setModules] = useState({});
  const [editingOption, setEditingOption] = useState(null);

  // MOCK API RESPONSE (you will replace with your decryptedData.rows)
  const sampleResponse = [
    {
      group_type: "driver",
      field_name: "driver_status",
      field_values: ["Active", "On Leave", "Suspended", "Retired"],
      id: 9,
    },
    {
      group_type: "driver",
      field_name: "gender",
      field_values: ["Male", "Female", "Other"],
      id: 8,
    },
    {
      group_type: "driver",
      field_name: "license_type",
      field_values: ["LMV", "HMV", "Transport", "Commercial"],
      id: 10,
    },

    {
      group_type: "location_master",
      field_name: "location_type",
      field_values: ["Pickup Point", "Drop Point", "Factory", "Office"],
      id: 7,
    },
    {
      group_type: "location_master",
      field_name: "zone",
      field_values: ["North", "South", "East", "West"],
      id: 11,
    },

    {
      group_type: "vehicle_master",
      field_name: "vehicle_type",
      field_values: ["New", "Old", "Outside", "Market"],
      id: 6,
    },
    {
      group_type: "vehicle_master",
      field_name: "vehicle_class",
      field_values: ["Commercial", "Passenger"],
      id: 5,
    },
    {
      group_type: "vehicle_master",
      field_name: "fuel_type",
      field_values: ["Petrol", "Diesel", "Electric", "CNG"],
      id: 12,
    },

    {
      group_type: "user_master",
      field_name: "roles",
      field_values: ["Admin", "Manager", "Supervisor", "Viewer"],
      id: 4,
    },
    {
      group_type: "user_master",
      field_name: "department",
      field_values: ["Transport", "Operations", "Accounts", "HR"],
      id: 13,
    },

    {
      group_type: "vehicle",
      field_name: "payment_details",
      field_values: ["UPI", "Cash", "Bank Transfer"],
      id: 3,
    },
    {
      group_type: "vehicle",
      field_name: "registration_type",
      field_values: ["Private", "Commercial"],
      id: 14,
    },
    {
      group_type: "vehicle",
      field_name: "gender",
      field_values: ["Male", "Female", "Other"],
      id: 1,
    },
  ];

  useEffect(() => {
    setData(sampleResponse);

    const grouped = sampleResponse.reduce((acc, item) => {
      if (!acc[item.group_type]) acc[item.group_type] = [];
      acc[item.group_type].push(item);
      return acc;
    }, {});

    setModules(grouped);
  }, []);

  // ADD NEW OPTION
  const handleAddOption = (moduleKey, fieldIndex) => {
    const newValue = prompt("Enter new option:");
    if (!newValue) return;

    const updated = { ...modules };
    updated[moduleKey][fieldIndex].field_values.push(newValue);

    setModules(updated);
  };

  // DELETE OPTION
  const handleDeleteOption = (moduleKey, fieldIndex, optionIndex) => {
    if (!confirm("Delete this option?")) return;

    const updated = { ...modules };
    updated[moduleKey][fieldIndex].field_values.splice(optionIndex, 1);

    setModules(updated);
  };

  // START EDIT OPTION
  const startEditing = (moduleKey, fieldIndex, optionIndex, value) => {
    setEditingOption({ moduleKey, fieldIndex, optionIndex, value });
  };

  // SAVE EDITED OPTION
  const saveEdit = () => {
    const { moduleKey, fieldIndex, optionIndex, value } = editingOption;
    const updated = { ...modules };

    updated[moduleKey][fieldIndex].field_values[optionIndex] = value;

    setModules(updated);
    setEditingOption(null);
  };

  return (
    <div className="p-6">
      <h1 className="text-2xl font-semibold mb-6">Dropdown Configuration</h1>

      {Object.keys(modules).map((moduleKey) => (
        <div key={moduleKey} className="mb-4">
          {/* MODULE TITLE */}
          <h2 className="text-xl font-bold mb-2 capitalize">
            {moduleKey.replace("_", " ")}
          </h2>

          {modules[moduleKey].map((field, fieldIndex) => (
            <Accordion key={field.id} sx={{ mb: 1 }}>
              <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                <span className="font-medium">{field.field_name}</span>
              </AccordionSummary>

              <AccordionDetails>
                {field.field_values.map((option, optionIndex) => {
                  const isEditing =
                    editingOption &&
                    editingOption.moduleKey === moduleKey &&
                    editingOption.fieldIndex === fieldIndex &&
                    editingOption.optionIndex === optionIndex;

                  return (
                    <div
                      key={optionIndex}
                      className="flex items-center justify-between py-2 border-b"
                    >
                      {/* OPTION TEXT / INPUT */}
                      {isEditing ? (
                        <input
                          type="text"
                          className="border p-1 rounded w-1/2"
                          value={editingOption.value}
                          onChange={(e) =>
                            setEditingOption({
                              ...editingOption,
                              value: e.target.value,
                            })
                          }
                        />
                      ) : (
                        <span>{option}</span>
                      )}

                      <div className="flex gap-3 items-center">
                        {isEditing ? (
                          <>
                            <SaveIcon
                              className="cursor-pointer text-green-600"
                              onClick={saveEdit}
                            />
                            <CloseIcon
                              className="cursor-pointer text-gray-600"
                              onClick={() => setEditingOption(null)}
                            />
                          </>
                        ) : (
                          <>
                            <EditIcon
                              className="cursor-pointer text-blue-600"
                              onClick={() =>
                                startEditing(
                                  moduleKey,
                                  fieldIndex,
                                  optionIndex,
                                  option
                                )
                              }
                            />
                            <DeleteIcon
                              className="cursor-pointer text-red-500"
                              onClick={() =>
                                handleDeleteOption(
                                  moduleKey,
                                  fieldIndex,
                                  optionIndex
                                )
                              }
                            />
                          </>
                        )}
                      </div>
                    </div>
                  );
                })}

                {/* ADD OPTION BUTTON */}
                <button
                  onClick={() => handleAddOption(moduleKey, fieldIndex)}
                  className="mt-3 flex items-center gap-2 text-blue-600"
                >
                  <AddIcon /> Add Option
                </button>
              </AccordionDetails>
            </Accordion>
          ))}
        </div>
      ))}
    </div>
  );
}
